import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';
import Simulator from '@/components/Simulator';
import Ticket from '@/components/Ticket';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/lc3/',
      name: 'LC3 Simulator',
      component: Simulator,
    },
    {
      path: '/ttr/',
      name: 'Ticket To Ride',
      component: Ticket,
    },
  ],
});



